import React from "react";
import * as S from "./Media.styles";

const photosLink = "https://drive.google.com/drive/folders/16GKRkj1p4Jn1QLN28HsXqernf_6iswPd?usp=sharing";
const moviesLink = "https://drive.google.com/drive/folders/1xa_9bTOFSMKsQwua_km_lUw9yESgwwiY?usp=drive_link";

const Media: React.FC = () => (
    <S.SectionWrapper id="media">
        <S.StyledSectionTag text="MEDIA" />
        <S.SectionText>Tutaj znajdziesz <S.BoldText>wspomnienia z naszego lubu</S.BoldText></S.SectionText>
        <S.Info>W tym miejscu, na przestrzeni okoo 3 miesicy, udostpnione zostan zdjcia i filmy z naszego wielkiego dnia, ktry chcemy zapamita na zawsze  razem z Wami. Przeyjmy te magiczne chwile jeszcze raz! Przegldaj, pobieraj, dziel si i wspominaj. To wanie dziki Wam ten dzie by tak wyjtkowy.</S.Info>
        <S.ButtonsWrapper>
            <S.Button href={photosLink} target="_blank">Zdjcia</S.Button>
            <S.Button href={moviesLink} target="_blank">Filmy</S.Button>
        </S.ButtonsWrapper>
    </S.SectionWrapper>
)

export default Media
