import React from "react";
import * as S from "./Menu.styles";
import { ReactComponent as LeafIcon } from "../assets/leaf.svg";

const obiad = [
    { title: "DANIE I", foods: ["Ros Krlewski"], wege: "Krem pomidorowy z grzankami" },
    { title: "DANIE II", foods: ["Pmisek mis: Tradycyjny kotlet schabowy, Devolay, Grilowany filet z kurczaka nadziewany szynk oraz mascarpone, Piecze z karkwki", "Ziemniaki z koprem"], wege: "Kotlet z batata" },
    { title: "DODATKI", foods: ["Surwka marchewka z mandarynk", "Surwka z czerwonej kapusty z sosem jogurtowo - majonezowym", "Mizeria"], },
    { title: "DESER", foods: ["Fondant czekoladowy z lodami i fruelin z wini"] },
]

const kolacje = [
    { title: "KOLACJA I", foods: ["Zrazy z warzywami w sosie z kluskami lskimi"], wege: "Papryka nadziewana ryem i warzywami" },
    { title: "KOLACJA II", foods: ["Bueczka Bao z szarpan woowin"], wege: "Bueczka Bao z warzywami" },
    { title: "KOLACJA III", foods: ["Strogonow"], wege: "Krem z cukinii z grznkami" },
    { title: "KOLACJA IV", foods: ["Barszcz czerwony z pierokiem krucho - drodowym"], wege: "Barszcz czerwony z pizzerink" },
    { title: "TORT", foods: ["Z musem z biaej czekolady, fruelin malinow i chrupk z nutell"] }
]

const Menu: React.FC = () => (
    <S.SectionWrapper id="menu">
        <S.StyledSectionTag text="MENU" />
        <S.SectionText>Sprawd, co <S.BoldText>pysznego</S.BoldText> dla Ciebie przygotowalimy! Smacznego!</S.SectionText>
        <S.SectionSubText>Poniej znajdziesz menu obiadowe oraz menu z daniami na ciepo, ktre bd serwowane w trakcie przyjcia. Zachcamy rwnie do odwiedzenia sodkiego stou, penego ciast, deserw i innych akoci, a take wiejskiego stou, gdzie czekaj rnorodne przekski i przysmaki. Niech to bdzie uczta nie tylko dla serca, ale i dla podniebienia!</S.SectionSubText>
        <S.StyledCarousel showArrows={false}>
            <S.CenterWrapper>
                <S.Menu>
                    <S.MenuTitle>Obiad</S.MenuTitle>
                    <S.MenuItems>
                        {obiad.map(({ title, foods, wege }) => (
                            <S.MenuItem key={title}>
                                <S.ItemName>{title}</S.ItemName>
                                <S.ItemFoods>
                                    {foods?.map(food => <S.Food key={food}>{food}</S.Food>)}
                                    {!wege ? null : (
                                        <S.ItemWege>
                                            <S.WegeIcon>
                                                <LeafIcon />
                                            </S.WegeIcon>
                                            {wege}
                                        </S.ItemWege>
                                    )}
                                </S.ItemFoods>
                            </S.MenuItem>
                        ))}
                    </S.MenuItems>
                </S.Menu>
            </S.CenterWrapper>
            <S.CenterWrapper>
                <S.Menu>
                    <S.MenuTitle>Kolacje</S.MenuTitle>
                    <S.MenuItems>
                        {kolacje.map(({ title, foods, wege }) => (
                            <S.MenuItem key={title}>
                                <S.ItemName>{title}</S.ItemName>
                                <S.ItemFoods>
                                    {foods?.map(food => <S.Food key={food}>{food}</S.Food>)}
                                    {!wege ? null : (
                                        <S.ItemWege>
                                            <S.WegeIcon>
                                                <LeafIcon />
                                            </S.WegeIcon>
                                            {wege}
                                        </S.ItemWege>
                                    )}
                                </S.ItemFoods>
                            </S.MenuItem>
                        ))}
                    </S.MenuItems>
                </S.Menu>
            </S.CenterWrapper>
        </S.StyledCarousel>
        <S.DesktopMenus>
            <S.CenterWrapper>
                <S.Menu>
                    <S.MenuTitle>Obiad</S.MenuTitle>
                    <S.MenuItems>
                        {obiad.map(({ title, foods, wege }) => (
                            <S.MenuItem key={title}>
                                <S.ItemName>{title}</S.ItemName>
                                <S.ItemFoods>
                                    {foods?.map(food => <S.Food key={food}>{food}</S.Food>)}
                                    {!wege ? null : (
                                        <S.ItemWege>
                                            <S.WegeIcon>
                                                <LeafIcon />
                                            </S.WegeIcon>
                                            {wege}
                                        </S.ItemWege>
                                    )}
                                </S.ItemFoods>
                            </S.MenuItem>
                        ))}
                    </S.MenuItems>
                </S.Menu>
            </S.CenterWrapper>
            <S.CenterWrapper>
                <S.Menu>
                    <S.MenuTitle>Kolacje</S.MenuTitle>
                    <S.MenuItems>
                        {kolacje.map(({ title, foods, wege }) => (
                            <S.MenuItem key={title}>
                                <S.ItemName>{title}</S.ItemName>
                                <S.ItemFoods>
                                    {foods?.map(food => <S.Food key={food}>{food}</S.Food>)}
                                    {!wege ? null : (
                                        <S.ItemWege>
                                            <S.WegeIcon>
                                                <LeafIcon />
                                            </S.WegeIcon>
                                            {wege}
                                        </S.ItemWege>
                                    )}
                                </S.ItemFoods>
                            </S.MenuItem>
                        ))}
                    </S.MenuItems>
                </S.Menu>
            </S.CenterWrapper>
        </S.DesktopMenus>
    </S.SectionWrapper>
)

export default Menu
