const parseName = (name: string) => name.replace(/ /g, '').toLocaleLowerCase();

const getTable = (argName: string) => {
    const [tableNumber, table] = Object.entries(miejsca).find(([, names]) => names.some(name => parseName(name) === argName)) ?? [];

    if (table) {
        return {
            tableNumber,
            table,
        }
    }

    return undefined;
}

const TableForm: React.FC = () => {
    const [name, setName] = useState("");
    const [surname, setSurname] = useState("");
    const [isResultVisible, setIsResultVisible] = useState(false)

    const onInputChange = (fieldName: string) => (event: any) => {
        if (fieldName === "name") {
            setName(event.target?.value?.trim() ?? "")
        } else {
            setSurname(event.target?.value?.trim() ?? "")
        }
    }

    const onBlur = () => {
        if (name && surname) {
            setIsResultVisible(true);
        }
    }

    const onFocus = () => {
        setIsResultVisible(false);
    }

    const searchString = parseName(`${name}${surname}`);
    const { tableNumber, table } = getTable(searchString) ?? {};

    const halfTable = Math.ceil((table?.length ?? 0) / 2);

    const leftSeats = table?.slice(0, halfTable);
    const rightSeats = table?.slice(halfTable);

    return (
        <S.SectionWrapper>
            <S.TextAndFromWrapper>
                <S.SectionTitle>Wypenij formularz</S.SectionTitle>
                <S.SectionText>Wpisz swoje imi i nazwisko, a system wskae Twoje miejsce, dziki temu szybciej odnajdziesz si po przybyciu na sal i bdziesz mie wicej czasu na zabaw!</S.SectionText>
                <S.FormWrapper>
                    <S.InputWrapper>
                        <S.InputLabel>Imi</S.InputLabel>
                        <S.Input placeholder={randomNumber === 0 ? "Konrad" : "Gabriela"} onChange={onInputChange("name")} value={name} onBlur={onBlur} onFocus={onFocus} />
                    </S.InputWrapper>
                    <S.InputWrapper>
                        <S.InputLabel>Nazwisko</S.InputLabel>
                        <S.Input placeholder={randomNumber === 0 ? "Pasik" : "Florka"} onChange={onInputChange("surname")} value={surname} onBlur={onBlur} onFocus={onFocus} />
                    </S.InputWrapper>
                </S.FormWrapper>
            </S.TextAndFromWrapper>
            <S.Result>
                {isResultVisible && (
                    tableNumber && table ? (
                        <>
                            <S.SeatsWrapper>{leftSeats?.map((name) => <S.Seat highlight={parseName(name) === searchString} key={name} />)}</S.SeatsWrapper>
                            <S.TableName>St {tableNumber}</S.TableName>
                            <S.SeatsWrapper>{rightSeats?.map((name) => <S.Seat highlight={parseName(name) === searchString} key={name} />)}</S.SeatsWrapper>
                        </>
                    ) : "Nie udao si odnale miejsca"
                )}
            </S.Result>
        </S.SectionWrapper>
    )
}

export default TableForm
